import React from 'react';
import './Sidebar.css';
import SidebarOption from './SidebarOption';
import TwitterIcon from '@material-ui/icons/Twitter';
import HomeIcon from '@material-ui/icons/Home';

function Sidebar() {
    return (
        <div className="sidebar">
            { /* icon */ }
            <TwitterIcon className="sidebar--appicon"/>

            <SidebarOption active Icon={HomeIcon} text="Home"/>
            <SidebarOption Icon={HomeIcon} text="Conta"/>
            <SidebarOption Icon={HomeIcon} text="Notificações"/>
            <SidebarOption Icon={HomeIcon} text = "Desconectar"/>
            { /* SidebarOption */ }
            { /* SidebarOption */ }
            { /* SidebarOption */ }
            { /* SidebarOption */ }
        </div>
    )
}

export default Sidebar;
