import React, {useState} from 'react';
import './TweetBox.css';
import { Avatar, Button } from '@material-ui/core';
import db from './firebase'

function TweetBox() {
    const [tweetMessage, setTweetMessage] = useState('');
    const [tweetName, setTweetName] = useState('');
    const [tweetUsername, setTweetUsername] = useState('');

    const sendTweet = e => {
        e.preventDefault();

        db.collection('posts').add({
            displayName: tweetName,
            username: tweetUsername,
            verified: true,
            text: tweetMessage,
            image: "",
            avatar: "https://pbs.twimg.com/profile_images/1293925081542995971/s2la3KS9_400x400.png"
        });

        setTweetMessage("");
    }

    return (
        <div className="tweetBox">
            <form>
                <div className="tweetBox--input">
                    <Avatar src="https://pbs.twimg.com/profile_images/1293925081542995971/s2la3KS9_400x400.png" />
                    <input onChange={(e) => setTweetMessage(e.target.value)}
                    value={tweetMessage} placeholder="What's happening?" type="text"></input>
                </div>
                <input onChange={(e) => setTweetName(e.target.value)}
                value={tweetName}
                className="tweetBox--imageInput" placeholder="Nome" type="text"></input>
                <input onChange={(e) => setTweetUsername(e.target.value)}
                value={tweetUsername}
                className="tweetBox--imageInput" placeholder="Username (@exemplo)" type="text"></input>
                <Button onClick={sendTweet} type="submit" class="tweetBox--tweetButton">Tweet</Button>
            </form>
        </div>
    )
}

export default TweetBox
